import './index.css'
import { take } from 'lodash'
import hbs from './index.hbs'
import { fetchInterval, fetch } from '@/util/request'
import { ModalServer } from '@/components'

const $ = window.$

export default class ThreatenList {
  constructor() {
    this.currentPage = 0
    this.currentId = null
  }

  /**
   * 绑定按钮的点击事件
   * @param  {Object} btn   按钮
   * @param  {String} type  按钮标识
   * @return {void}   void
   */
  bindClickEvent = (btn, type) => {
    btn.off('click').on('click', e => {
      const id = e.target.dataset.id

      const url = 'fetchThreatenDetail'
      const limit = 8

      // 点击时初始化当前页码
      this.currentPage = 0

      fetch(url, { type, id, limit, currentPage: this.currentPage }, dataSource => {
        const modal = new ModalServer({
          headers: ['名称', '公司', '主机IP', '攻击类型', '危险等级'],
          // 点击上一页
          onPrev: () => {
            if (this.currentPage > 0) {
              this.currentPage -= 1
              console.log(this.currentPage)
              fetch(url, { type, id, limit, currentPage: this.currentPage }, data => {
                modal.renderTable(data.list)
              })
            } else {
              alert('这是第一页!')
            }
          },
          // 点击下一页
          onNext: () => {
            this.currentPage += 1
            console.log(this.currentPage)
            fetch(url, { type, id, limit, currentPage: this.currentPage }, data => {
              if (data.list.length > 0) {
                modal.renderTable(data.list)
              } else {
                this.currentPage -= 1
                alert('这是最后一页!')
              }
            })
          }
        })
        modal.init(dataSource.list)
      }, 1000)

    })
  }

  /**
   * 定义上传逻辑
   * @return {void} void
   */
  upload = () => {
    const input = $('#uploadForm input[type=file]')
    const filenameDom = $('#uploadForm .file-name')
    const uploadButton = $('#uploadForm button')
    const dialogBtnClose = $('#threatenListContainer .dialog-upload .btn-close')

    // 绑定关闭按钮事件
    dialogBtnClose.off('click').on('click', () => {
      this.hideDialog()
    })

    // 选择文件后, 显示文件的名称
    input.off('change').on('change', e => {
      const filename = e.currentTarget.files[0].name
      filenameDom.html(filename)
    })

    // 上传按钮点击事件
    uploadButton.off('click').on('click', () => {
      if (!input[0].files.length) {
        alert('请选择文件')
        return
      }
      const file = input[0].files[0]
      // let formdata = new FormData()
      // formdata.append('file', file)
      // console.log('id:', this.currentId)
      // console.log('file:', file)
      // console.log('formdata:', formdata)
      fetch('fetchThreatenUpload', { id: this.currentId, formdata: file }, data => {
        alert('上传成功')
        $('.dialog-upload').addClass('hide')
        $('#threatenListContainer .col').each(d => {
          let atName = $('#threatenListContainer .col').eq(d).find('li').eq(0).html()
          if(data.name === atName) {
            $('#threatenListContainer .col').eq(d).find('li').eq(4).find('a').attr('href', data.filePath)
          }
        })
        this.clearInputFileValue()
      })
    })
  }

  /**
   * 清空 input file 中的值
   * @return {void} void
   */
  clearInputFileValue = () => {
    const input = $('#uploadForm input[type=file]')
    const filenameDom = $('#uploadForm .file-name')

    input[0].value = ''
    filenameDom.html('请选择文件')
  }

  showDialog = (id) => {
    const dialog = $('#threatenListContainer .dialog-upload')
    dialog.removeClass('hide')
    this.currentId = id
  }

  hideDialog = () => {
    this.clearInputFileValue()

    const dialog = $('#threatenListContainer .dialog-upload')

    dialog.addClass('hide')
    this.currentId = null
  }

  render(admin) {
    // 上传文件方法
    this.upload()
    // 渲染列表
    fetchInterval('fetchThreatenList', { limit: 2, currentPage: 0 }, data => {      
      // 只取前两个对象
      const table = $('#threatenListContainer .table')
      data.list.forEach(d => {
        if(d.source === '部文件') {
          d.judge = true
        } else {
          d.judge = false
        }
      })
      table.empty().append(hbs(take(data.list, 2)))
      if(data.list.length === 0){
        table.append('<span class="com-hint">暂无数据</span>')
      }
      // 判断是否是管理员
      if(admin != '1') {
        $('.upload, .attachment').css('pointer-events', 'none')
        $('.upload span').css({'background': '#716969', 'color': '#ddd'})
        $('.attachment a').css({'color': '#8a8989'})
      }
      const attackBtn = table.find('.attack')
      const interceptBtn = table.find('.intercept')

      // 绑定 攻击数 和 拦截查杀数 的弹框事件
      this.bindClickEvent(attackBtn, 'attack')
      this.bindClickEvent(interceptBtn, 'intercept')

      // 点击上传按钮, 显示上传弹框, 并将该条目 id 置于全局
      const uploadBtn = table.find('.upload span')
      uploadBtn.off('click').on('click', e => {

        const dialog = $('#threatenListContainer .dialog-upload')
        const id = e.currentTarget.dataset.id

        if (dialog.hasClass('hide')) {
          this.showDialog(id)
        } else {
          this.hideDialog()
        }
      })
    })
  }
}



