import './index.css'

import echarts from 'echarts'
import 'echarts-liquidfill'

import { cloneDeep } from 'lodash'
import hbs from './index.hbs'
import { fetchInterval } from '@/util/request'
import { gaugeOpt, liquidfillOpt, liquidfillPanelOpt } from './options'
// import { getMousePosition } from '@/components/util/util.js'
import { deformationToOrigin } from '@/util/zoom/zoom'

const $ = window.$

export default class SafetyIndex {
  /**
   * 渲染网络安全指数
   * @param  {Object} data data
   * @return {void}   void
   */
  renderNetwork = data => {
    for (let i = 0; i < 3; i ++) {
      const gauge = echarts.init($(`#gauge${i + 1}`)[0])
      let option = cloneDeep(gaugeOpt)
      option.series.data = [data.detail[i]]
      gauge.setOption(option)
    }
    console.log(data)
  }

  /**
   * 渲染终端安全指数
   * @param  {Object} data data
   * @return {void}   void
   */
  renderTerminal = data => {
    for (let i = 0; i < 3; i ++) {
      const liquidfill = echarts.init($(`#liquidfill${i + 1}`)[0])
      let option = cloneDeep(liquidfillOpt)
      option.series.data[0].value = data.detail[i].value / 100
      option.series.data[1].value = data.detail[i].value / 100

      liquidfill.setOption(option)
    }
  }

  /**
   * 渲染数据安全指数
   * @return {void}   void
   */
  renderData = () => {
    for (let i = 0; i < 4; i ++) {
      const liquidfill = echarts.init($('.liquidfill-panel')[i])
      liquidfill.setOption(liquidfillPanelOpt)
    }
  }

  render() {
    fetchInterval('fetchSafetyIndex', data => {
      console.log(data)
      let newData = this.newData(data)
      $('#safeindexContainer').empty().append(hbs(newData))
      $('#topoSafetyIndex').html(data.topoValue)
      this.renderNetwork(data.network)
      this.renderTerminal(data.terminal)
      this.renderData()
      this.addTitle()
    }, 3600000)
  }

  /**
   *  转换数组，目的是添加title字段
   */
  newData(data){
    let terminalTitle = [
      '100 - ROUNDUP（（∑_(i=1)^N（当前终端病毒感染数 * 2 - 当前终端病毒查杀数 * 1））/ 终端总数，0）', 
      '100 - ROUNDUP（（∑_(i=1)^N（当前终端外联设备次数 * 30））/ 终端总数, 0）', 
      '100 - ROUNDUP（（∑_(i=1)^N（当前终端一机两用次数 *1 00））/ 终端总数， 0）'
    ]
    let dataTitle = [
      '100 - ROUNDUP(防数据泄漏风险值 = 防数据泄露模型告警次数 * 5, 0)', 
      '100 - ROUNDUP(防暴力破解风险值 = 防暴力破解模型告警次数 * 5, 0)', 
      '100 - ROUNDUP(防探测风险值 = 防探测模型告警次数 * 5, 0)',
      '100 - ROUNDUP(防高危操作风险值 = 防高危操作模型告警次数 * 5, 0)'
    ]
    data.terminal.detail.map((d, i) => {
      d.title = terminalTitle[i]
    })
    data.terminal.title = '终端安全指数 = 0.5防一机两用安全指数 + 0.3防外联设备安全指数 + 0.2防病毒攻击安全指数'
    data.data.detail.map((d, i) => {
      d.title = dataTitle[i]
    })
    data.data.title = '数据安全指数 = 0.3防数据泄露安全指数 + 0.25防暴力破解安全指数 + 0.25防探测安全指数 + 0.2防高危操作安全指数'
    console.log(data)
    return data
  }

  /**
   * 添加title
   */
  addTitle(){
    $('header h2, .net-safeindex-panel .item, .net-safeindex-panel .title, .terminal-safeindex-panel .item, .terminal-safeindex-panel .title, .data-safeindex-panel .item, .data-safeindex-panel .title')
    .on('mousemove', ev => {
      let Ev = ev || window.event
      let pos = deformationToOrigin([Ev.pageX , Ev.pageY])
      $('.com-floating .value').html($(Ev.currentTarget).attr('data-title'))
      $('.com-floating').stop(true, true).fadeIn(500)
      $('.com-floating').css({'transform': `translate(${pos[0] + 30}px, ${pos[1] + 30}px)`})
    }).on('mouseout', () => {
      $('.com-floating').stop(true, true).fadeOut(500)
    })
  }
}



