import echarts from 'echarts'

export default {
  series: {
    type: 'liquidFill',
    radius: '90%',
    outline: {
      show: false
    },
    backgroundStyle: {
      color: 'rgba(42, 35, 129, .6)'
    },
    label: {
      show: false
    },
    data: [
      {
        value: 0.5,
        itemStyle: {
          color: '#3d1d49'
        }
      },
      {
        value: 0.5,
        itemStyle: {
          color: new echarts.graphic.LinearGradient(
            0, 0, 0, 1,
            [
              { offset: 0, color: '#bb3532' },
              { offset: 0.5, color: '#c56d2a' },
              { offset: 1, color: '#c5a724' }
            ]
          )
        }
      }
    ]
  }
}



