/*
 * @Author: liqi@hiynn.com
 * @Date: 2018-07-18 22:17:35
 * @Description: 折线图配置
 * @Last Modified by: shuman
 * @Last Modified time: 2018-08-06 15:17:28
 */

const line = (data, name, color) => ({
  name,
  type: 'line',
  smooth: true,
  itemStyle: {
    color
  },
  lineStyle: {
    color
  },
  data
})

export default (domain, dataSource) => ({
  title: {
    show: false
  },
  tooltip: {
    trigger: 'axis',
    textStyle: {
      fontSize: 26
    }
  },
  legend: {
    data: [
      '安全指数',
      '网络安全',
      '终端安全',
      '数据安全'
    ],
    right: '6%',
    inactiveColor: '#4f4f4f',
    itemGap: 40,
    selected: {
      '安全指数': true,
      '网络安全': false,
      '终端安全': false,
      '数据安全': false
    },
    textStyle: {
      fontSize: 26,
      color: '#FFF'
    },
    icon: 'rect',
    itemWidth: 45,
    itemHeight: 5
  },
  grid: {
    left: '3%',
    right: '4%',
    bottom: '3%',
    containLabel: true
  },
  xAxis: {
    type: 'category',
    name: '日',
    nameLocation: 'start',
    nameGap: 50,
    nameTextStyle: {
      color: '#ffd200',
      fontSize: 26
    },
    axisLine: {
      lineStyle: {
        color: '#0248c4'
      }
    },
    axisTick: {
      inside: true,
      length: 220,
      lineStyle: {
        width: 4,
        color: 'rgba(0, 255, 255, .24)'
      }
    },
    axisLabel: {
      color: '#69beff',
      fontSize: 26
    },
    data: domain
  },
  yAxis: {
    type: 'value',
    scale: true,
    axisLine: {
      show: false
    },
    splitNumber: 4,
    axisTick: {
      show: false
    },
    axisLabel: {
      fontSize: 26,
      color: '#9dc9ff'
    },
    splitLine: {
      lineStyle: {
        width: 4,
        color: 'rgba(0, 255, 255, .24)'
      }
    },
    splitArea: {
      show: true,
      interval: 1,
      areaStyle: {
        color: ['transparent', 'rgba(0, 20, 144, .44)']
      }
    }
  },
  series: [
    line(dataSource.comprehensive, '安全指数', '#00ff00'),
    line(dataSource.network, '网络安全', '#ffff00'),
    line(dataSource.terminal, '终端安全', '#00ffff'),
    line(dataSource.data, '数据安全', '#c000ff')
  ]
})



