import echarts from 'echarts'

export default {
  series: {
    type: 'liquidFill',
    shape: 'rect',
    radius: '100%',
    outline: {
      show: false
    },
    backgroundStyle: {
      color: 'transparent'
    },
    label: {
      show: false
    },
    data: [
      {
        value: 0.8,
        itemStyle: {
          color: new echarts.graphic.LinearGradient(
            0, 0, 0, 1,
            [
              { offset: 0, color: 'rgba(0, 12, 255, .24)' },
              { offset: 1, color: 'rgba(241, 233, 246, .24)' }
            ]
          )
        }
      },
      {
        value: 0.8,
        itemStyle: {
          color: 'rgba(0, 96, 255, .24)'
        }
      }
    ]
  }
}



