import './index.css'
import { fetchInterval } from '@/util/request'
import { Modal } from '@/components'
import listHbs from './templates/list.hbs'

const $ = window.$

export default class Warning {
  render() {
    let dataSource = null

    fetchInterval('fetchWarning', data => {
      dataSource = data.list
      if(dataSource.length === 0) {
        $('#warningListWrapper').empty().append('<span class="com-hint">系统安全，无告警信息</span>')
      }
      // 添加列表
      $('#warningList')
        .empty()
        .append(listHbs(data.list))
        .liMarquee()
    }, 600000)

    $('#btnWarningDetail').on('click', () => {
      const modal = new Modal(['ID', '告警类型', '告警内容', '处置情况', '主机IP'])
      modal.render(dataSource)

      $('#btnModalClose').on('click', function() {
        $(this).off('click')
        $('#modalContainer').remove()
      })
    })
  }
}



