import './index.css'
import * as hyflow from 'hyflow'
import { includes, min } from 'lodash'
import { fetchInterval } from '@/util/request'
import { IPS, systemZone, terminalZone, safetyMonitorZone } from '../../nodeKeys'
import hbs from './index.hbs'
import flowOpt from './flowOpt'

import tooltipBgSrc from './images/tooltip-bg.png'
import nodePaneWarningSrc from './images/node-pane-warning.png'
import nodePaneSrc from './images/node-pane.png'

import sanyawanFrames from './images/sanyawan-theme'
import shuituFrames from './images/shuitu-theme'

const $ = window.$

export default class Topo {
  constructor() {
    const pageWidth = 3360
    const pageHeight = 1890

    const flowWidth = 2434
    const flowHeight = 1186

    const xScale = window.innerWidth / pageWidth
    const yScale = window.innerHeight / pageHeight

    const transWidth = flowWidth * xScale
    const transHeight = flowHeight * yScale

    const restoreXScale = 1 / xScale
    const restoreYScale = 1 / yScale

    $('#topoFlowContainer').append(hbs({
      width: transWidth, height: transHeight,
      xScale: restoreXScale, yScale: restoreYScale
    }))

    this.flow = new hyflow.Flow($('#topoFlow')[0], {
      onNodeMouseEnter: this.onNodeMouseEnter,
      onNodeMouseLeave: this.onNodeMouseLeave
    })
    this.flow.setOption(flowOpt)

    this.flow.diagram.minScale = 0.01
    this.flow.diagram.initialScale = min([xScale, yScale]) - 0.08

    // 定义提示框
    this.initTooltip()
  }

  /**
   * 定义提示框
   * @return {void} void
   */
  initTooltip = () => {
    const tooltipW = 300
    const tooltipH = 200
    this.tooltip = document.createElement('div')
    this.tooltip.style.width = `${tooltipW}px`
    this.tooltip.style.height = `${tooltipH}px`
    this.tooltip.style.transform = 'translate(-50%, -50%)'
    this.tooltip.style.position = 'absolute'
    this.tooltip.style.left = `${tooltipW / 2}px`
    this.tooltip.style.top = `${tooltipH / 2}px`
    this.tooltip.style.zIndex = '100'
    this.tooltip.style.padding = '24px'
    this.tooltip.style.background = `url(${tooltipBgSrc}) no-repeat center / 100% 100%`
  }

  /**
   * 制作提示框的内容
   * @param  {String} key 节点的 key
   * @return {String} 返回内容
   */
  makeTooltipContent = key => {
    let content
    switch (key) {
    case 'sanyawan-invade':
      content = '入侵监测：通过实时监控网络流量来定位和识别恶意流量或攻击行为，分析来自网络外部和内部的入侵信号。在业务系统受到危害之前发出警告并实时对攻击做出阻断响应，针对网络、主机系统及应用程序提供主动防护措施。'
      break
    case 'sanyawan-sandbox':
      content = '沙箱监测：在完全封闭和安全的环境中，模拟各类应用的安装、使用，过滤恶意软件和阻断攻击访问。'
      break
    case 'sanyawan-database':
      content = '数据库审计：实时记录网络上的数据库活动，对数据库操作进行细粒度审计的合规性管理，对数据库遭受到的风险行为进行告警，对攻击行为进行阻断。通过对用户访问数据库行为的记录、分析和建模，实现核心数据访问可管、可控、可溯源。'
      break
    case 'sanyawan-virus':
      content = '终端病毒监测：精确检测已知病毒木马、未知恶意代码，有效防御APT攻击，为电脑终端提供全方位安全防护。'
      break
    case 'sanyawan-bug':
      content = '漏洞监测	对全网终端漏洞进行扫描并关联，根据终端分组或操作系统类型错峰下发补丁。'
      break
    case 'sanyawan-virtual':
      content = '虚拟化监测：对虚拟化服务器，虚拟化桌面进行实时安全监测，对虚拟化服务和虚拟化桌面遭受到的风险行为进行告警，对攻击行为进行阻断。'
      break
    default:
      content = '暂无内容'
      break
    }

    return content
  }

  /**
   * 鼠标悬浮节点事件
   * @param  {Object} obj  obj
   * @param  {Object} data data
   * @return {void}   void
   */
  onNodeMouseEnter = (obj, data) => {
    if (!includes(safetyMonitorZone, data.key)) {
      return
    }

    const content = this.makeTooltipContent(data.key)

    this.tooltip.innerHTML = `
        <p style="padding: 12px 16px;color: #FFF;font-size: 12px;">
        ${content}
        </p>
      `

    this.flow.diagram.div.appendChild(this.tooltip)
  }

  /**
   * 鼠标离开节点事件
   * @param  {Object} obj  obj
   * @param  {Object} data data
   * @return {void}   void
   */
  onNodeMouseLeave = (obj, data) => {
    if (!includes(safetyMonitorZone, data.key)) {
      return
    }

    this.flow.diagram.div.removeChild(this.tooltip)
  }

  /**
   * 设置边界警告节点的属性
   * @return {void}   void
   */
  setIPSWarningNodes = () => {
    fetchInterval('fetchTopoGetIPSWarning', data => {
      // 将所有未警告的节点恢复
      for (let key of IPS) {
        this.flow.setDataProperty('node', key, 'icon', d => {
          d.bgsrc = nodePaneSrc
          return d
        })
      }

      // 将警告的节点标红
      if (data.list.length) {
        for (let key of data.list) {
          this.flow.setDataProperty('node', key, 'icon', d => {
            d.bgsrc = nodePaneWarningSrc
            return d
          })
        }
      }
    }, 60000)
  }

  setWarningNodes = (url, nodeKeys, color) => {
    fetchInterval(url, data => {
      // 将所有未警告的节点恢复
      for (let key of nodeKeys) {
        this.flow.setDataProperty('node', key, 'label', d => {
          d.fontColor = '#FFF'
          return d
        })
      }

      // 将警告的节点标红
      if (data.list.length) {
        for (let key of data.list) {
          this.flow.setDataProperty('node', key, 'label', d => {
            d.fontColor = color
            return d
          })
        }
      }
    }, 60000)
  }

  render() {
    // 边界警告信息展示
    this.setIPSWarningNodes()

    // 业务系统区告警信息展示
    this.setWarningNodes('fetchTopoGetSystemWarning', systemZone, 'red')

    // 终端区告警信息展示
    this.setWarningNodes('fetchTopoGetTerminalWarning', terminalZone, 'red')

    // 设置三亚湾动画
    this.sanyawanFrame = 1
    setInterval(() => {
      if (this.sanyawanFrame === 17) {
        this.sanyawanFrame = 1
      }

      this.flow.setDataProperty('node', 'sanyawan-theme', 'icon', d => {
        d.bgsrc = sanyawanFrames[this.sanyawanFrame - 1]
        return d
      })
      this.sanyawanFrame += 1
    }, 100)

    // 设置水土局动画
    this.shuituFrame = 1
    setInterval(() => {
      if (this.shuituFrame === 17) {
        this.shuituFrame = 1
      }

      this.flow.setDataProperty('node', 'shuitu-theme', 'icon', d => {
        d.bgsrc = shuituFrames[this.shuituFrame - 1]
        return d
      })
      this.shuituFrame += 1
    }, 100)
  }
}



