import Mock from 'mockjs'

import { IPS, terminalZone, systemZone } from '../nodeKeys'

// 权限
import fetchLoginUsername from './loginUsername'
import fetchLogOut from './logOut'

import fetchSafetyIndex from './safetyIndex'
import fetchThreatenList from './threatenList'
import fetchThreatenDetail from './threatenDetail'
import fetchThreatenUpload from './threatenUpload'
import fetchTopoFloating from './topoFloating'
import fetchSafeTrend from './safeTrend'
import fetchTopoGetIPSWarning from './topogetIPSWarning'
import fetchTopoGetSystemWarning from './topogetSystemWarning'
import fetchTopoGetTerminalWarning from './topogetTerminalWarning'
import fetchWarning from './warning'

Mock.Random.extend({
  virusNames: function () {
    const names = ['永恒之蓝', '熊猫烧香', '蠕虫病毒']
    return this.pick(names)
  },
  dangerlevel: function () {
    const levels = ['低', '中', '高']
    return this.pick(levels)
  },
  company: function () {
    const names = ['中国移动', '中国电信', '海云数据', '阿里巴巴', '腾讯', '滴滴打车', '饿了么']
    return this.pick(names)
  },
  resolve: function() {
    const resolves = ['已处理', '待处理', '正在处理']
    return this.pick(resolves)
  },
  IPS: function () {
    const ids = IPS
    return this.pick(ids)
  },
  terminalZone: function () {
    const ids = terminalZone
    return this.pick(ids)
  },
  systemZone: function () {
    const ids = systemZone
    return this.pick(ids)
  },
  warningContent: function () {
    const content = ['永恒之蓝病毒大举入侵, 内网已瘫痪!', '公安内网发现蠕虫病毒, 请相关部门尽快处理', '公安内网发现疑似木马病毒, 正在处理中!']
    return this.pick(content)
  }
})

export default {
  fetchLoginUsername,
  fetchLogOut,
  
  fetchSafetyIndex,
  fetchThreatenList,
  fetchThreatenDetail,
  fetchThreatenUpload,
  fetchTopoFloating,
  fetchSafeTrend,

  fetchTopoGetIPSWarning,
  fetchTopoGetSystemWarning,
  fetchTopoGetTerminalWarning,

  fetchWarning
}



