/*
 * @Author: liqi@hiynn.com
 * @Date: 2018-07-19 11:37:40
 * @Description: 流程图配置项
 * @Last Modified by: liqi@hiynn.com
 * @Last Modified time: 2018-08-22 18:21:33
 */
import divider1src from './images/divider-1.png'

import group1src from './images/group-1.png'
import group2src from './images/group-2.png'
import groupTitleSrc from './images/group-title.png'

import nodeIPSsrc from './images/node-IPS.png'
import nodePaneSrc from './images/node-pane.png'
import nodeServerSrc from './images/node-server.png'
import nodeCloudDeskSrc from './images/node-cloud-desk.png'
import nodePCSrc from './images/node-pc.png'

import nodeMonitor1src from './images/node-monitor-1.png'
import nodeMonitor2src from './images/node-monitor-2.png'
import nodeMonitor3src from './images/node-monitor-3.png'
import nodeMonitor4src from './images/node-monitor-4.png'
import nodeMonitor5src from './images/node-monitor-5.png'

const nodeTheme = (key, loc) => ({
  key,
  category: 'icon',
  width: 370,
  height: 400,
  loc,
  icon: {
    width: 370,
    height: 400
  },
  label: {
    alignment: '0.5 0 -20 120',
    text: '核心交换',
    font: 'Normal Normal 22px Helvetica',
    fontColor: '#FFF'
  }
})

const nodeIPS = (key, loc) => ({
  key,
  category: 'icon',
  width: 150,
  height: 240,
  loc,
  icon: {
    width: 150,
    height: 182,
    bgsrc: nodeIPSsrc
  },
  label: {
    text: 'IPS',
    font: 'Bold Normal 48px Helvetica',
    fontColor: '#FFF'
  }
})

const nodePane = (key, text, loc) => ({
  key,
  category: 'icon',
  width: 210,
  height: 54,
  loc,
  icon: {
    width: 210,
    height: 54,
    bgsrc: nodePaneSrc
  },
  label: {
    alignment: 'center',
    text,
    font: 'Normal Normal 32px Helvetica',
    fontColor: '#FFF'
  }
})

const nodeServer = (key, group, src, text, loc) => ({
  key,
  category: 'icon',
  group,
  width: 266,
  height: 100,
  loc,
  icon: {
    alignment: 'left',
    width: 60,
    height: 60,
    bgsrc: src
  },
  label: {
    alignment: 'right',
    text,
    font: 'Normal Normal 32px Helvetica',
    fontColor: '#FFF'
  },
  subLabel: {
    show: true,
    alignment: 'bottom',
    text: '',
    width: 280,
    height: 4,
    bgsrc: divider1src
  }
})

const nodeGroup = (key, text, size, src) => ({
  key,
  category: 'group',
  fill: 'transparent',
  stroke: 'transparent',
  image: {
    show: true,
    width: size[0],
    height: size[1],
    src
  },
  title: {
    alignment: 'left',
    text,
    width: 214,
    height: 64,
    font: 'Normal Normal 32px Helvetica',
    fontColor: '#ffd200',
    bgsrc: groupTitleSrc
  }
})

const nodeIcon = (key, group, size, src, loc) => ({
  key,
  category: 'icon',
  loc,
  group,
  width: size[0],
  height: size[1],
  icon: {
    width: size[0],
    height: size[1],
    bgsrc: src
  },
  label: {
    text: ''
  }
})

const nodeLable = (key, group, text, size, loc) => ({
  key,
  group,
  category: 'normal',
  width: size[0],
  height: size[1],
  fill: 'transparent',
  stroke: 'transparent',
  loc,
  label: {
    text,
    font: 'Normal Normal 32px Helvetica',
    fontColor: '#FFF'
  }
})

const link = ({from, to, toArrowVisible = true}) => ({
  key: new Date().valueOf(),
  from, to,
  corner: 10,
  stroke: '#72fffd',
  strokeWidth: 4,
  fromArrow: {
    show: false
  },
  toArrow: {
    show: toArrowVisible,
    scale: 2,
    figure: 'Circle',
    fill: '#6dedf3'
  }
})

export default {
  nodeDataArray: [
    nodeTheme('sanyawan-theme', '-250 70'),
    nodeTheme('shuitu-theme', '1320 70'),

    // 三亚湾机房 IPS
    nodeIPS('sanyawan-IPS-1', '-590 -210'),
    nodeIPS('sanyawan-IPS-2', '-590 180'),
    nodePane('police', '公安部', '-810 -260'),
    nodePane('detachment', '各区县支队', '-810 -160'),
    nodePane('IPS1', '无线边界1', '-810 30'),
    nodePane('IPS2', '无线边界2', '-810 110'),
    nodePane('policenet', '公安网边界', '-810 190'),
    nodePane('internet', '互联网边界', '-810 270'),
    nodePane('videonet', '视频网边界', '-810 350'),

    // 水土机房 IPS
    nodeIPS('shuitu-IPS-1', '1710 -200'),
    nodeIPS('shuitu-IPS-2', '1710 170'),
    nodePane('policeport', '警口', '1870 -200'),
    nodePane('switch', '电围', '1880 170'),

    // 三亚湾机房 业务服务器
    nodeGroup('sanyawanSysGroup', '业务系统区', [365, 420], group1src),
    nodeServer('sanyawanServer', 'sanyawanSysGroup', nodeServerSrc, '实体服务器', '263 -290'),
    nodeServer('sanyawanVirtualServer', 'sanyawanSysGroup', nodeServerSrc, '虚拟服务器', '263 -180'),
    nodeServer('sanyawanDatabaseServer', 'sanyawanSysGroup', nodeServerSrc, '数据库服务器', '263 -70'),

    // 水土机房 业务服务区
    nodeGroup('shuituSysGroup', '业务系统区', [365, 420], group1src),
    nodeServer('shuituServer', 'shuituSysGroup', nodeServerSrc, '实体服务器', '880 -290'),
    nodeServer('shuituVirtualServer', 'shuituSysGroup', nodeServerSrc, '虚拟服务器', '880 -180'),
    nodeServer('shuituDatabaseServer', 'shuituSysGroup', nodeServerSrc, '数据库服务器', '880 -70'),

    // 终端区
    nodeGroup('terminalGroup', '终端区', [365, 300], group1src),
    nodeServer('PCTerminal', 'terminalGroup', nodePCSrc, 'PC终端', '463 300'),
    nodeServer('notebookComputer', 'terminalGroup', nodeCloudDeskSrc, '笔记本', '463 380'),

    // 三亚湾机房 安全监测区
    nodeIcon('monitorGroup1', '', [1300, 300], group2src, '-280 590'),
    nodeIcon('monitor1', '', [116, 56], nodeMonitor1src, '-700 610'),
    nodeIcon('monitor2', '', [116, 56], nodeMonitor2src, '-400 610'),
    nodeIcon('monitor3', '', [116, 56], nodeMonitor3src, '-100 610'),
    nodeIcon('monitor4', '', [116, 56], nodeMonitor4src, '200 610'),
    nodeLable('sanyawan-threaten', '', '未知威胁监测', [210, 45], '-700 560'),
    nodeLable('sanyawan-invade', '', '入侵监测', [140, 45], '-800 670'),
    nodeLable('sanyawan-sandbox', '', '沙箱监测', [140, 45], '-610 670'),
    nodeLable('sanyawan-bastion', '', '堡垒机', [105, 45], '-400 560'),
    nodeLable('sanyawan-database', '', '数据库审计', [175, 45], '-400 670'),
    nodeLable('sanyawan-terminal', '', '终端安全监测', [210, 45], '-100 560'),
    nodeLable('sanyawan-virus', '', '病毒监测', [140, 45], '-190 670'),
    nodeLable('sanyawan-bug', '', '漏洞监测', [140, 45], '-20 670'),
    nodeLable('sanyawan-virtual', '', '虚拟化安全监测', [245, 45], '200 670'),

    // 水土机房 安全监测区
    nodeGroup('monitorGroup2', '安全监测区', [325, 300], group1src),
    nodeLable('shuitu-invade', 'monitorGroup2', '入侵监测', [140, 45], '880.5 530'),
    nodeIcon('monitor5', 'monitorGroup2', [116, 56], nodeMonitor5src, '880.5 652.5'),
    nodeLable('shuitu-database', 'monitorGroup2', '数据库审计', [175, 45], '880.5 712.5')
  ],
  linkDataArray: [
    link({ from: 'sanyawan-theme', to: 'sanyawan-IPS-1' }),
    link({ from: 'sanyawan-theme', to: 'sanyawan-IPS-2' }),
    link({ from: 'sanyawan-theme', to: 'sanyawanSysGroup'}),
    link({ from: 'sanyawan-theme', to: 'terminalGroup'}),
    link({ from: 'sanyawan-theme', to: 'monitorGroup1'}),

    link({ from: 'sanyawan-IPS-1', to: 'police', toArrowVisible: false }),
    link({ from: 'sanyawan-IPS-1', to: 'detachment', toArrowVisible: false }),

    link({ from: 'sanyawan-IPS-2', to: 'IPS1', toArrowVisible: false }),
    link({ from: 'sanyawan-IPS-2', to: 'videonet', toArrowVisible: false }),

    link({ from: 'shuitu-theme', to: 'shuituSysGroup'}),
    link({ from: 'shuitu-theme', to: 'monitorGroup2'}),
    link({ from: 'shuitu-theme', to: 'shuitu-IPS-1'}),
    link({ from: 'shuitu-theme', to: 'shuitu-IPS-2'}),

    link({ from: 'sanyawan-theme', to: 'shuitu-theme'}),
    link({ from: 'shuitu-theme', to: 'sanyawan-theme'})
  ]
}



