import './index.css'
import echarts from 'echarts'
import { fetchInterval } from '@/util/request'
import lineChartOpt from './lineChartOpt'
const $ = window.$

export default class SafeTrend {
  render() {
    fetchInterval('fetchSafeTrend', data => {
      const chart = echarts.init($('#safeTrendChart')[0])

      let domain = []
      for (let i = 0; i < data.comprehensive.length; i ++) {
        domain.push(i + 1)
      }

      chart.setOption(lineChartOpt(domain, data))
    },600000)
  }
}



