
import './index.css'
import loader from '@/loader/loader'
import apis from './mock/index'
import { fetch } from '@/util/request'
const $ = window.$
import LoadNav from '@/components/loadNav/index'

let loadNav = new LoadNav()
import {
  Header,
  SafetyIndex,
  Topo,
  ThreatenList,
  SafeTrend,
  Warning
} from './components'

// 组件
const header = new Header()
const safetyIndex = new SafetyIndex()
const topo = new Topo()
const threatenList = new ThreatenList()
const safeTrend = new SafeTrend()
const warning = new Warning()

loader.load({
  apis,
  init() {
    loadNav.init() //  页面导航
    $('.side-l-nav li:eq(0)').addClass('active')
    // 判断是否有用户登录
    fetch('fetchLoginUsername', data => {
      if(data.name != undefined) {
        window.admin = data.admin
        // 组件
        header.render(data)
        safetyIndex.render()
        topo.render()
        threatenList.render(data.admin)
        safeTrend.render()
        warning.render()
      } else {
        alert('请先登录')
        fetch('fetchLogOut', data => {
            $(window).attr('location', data.url)
        })
      }
      this.addEvent()
    })
  },
  // 绑定事件
  addEvent() {
    // 点击退出
    $('.user-box').on('click', '.log-out', () => {
      fetch('fetchLogOut', data => {
          $(window).attr('location', data.url)
      })
    })
  }
}, 3360, 1890)



