export default {
  'url': '/totalTrend/threatenList/:limit/:currentPage',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'list|8': [
        {
          'id': '@id',
          'name': '@virusNames',
          'attack|10-100': 10,
          'intercept|10-100': 10,
          'source|+1': ['部文件','360文件'],
          'attachment': 'http://xxx.pdf'
        }
      ]
    }
  }
}



