export default {
  series: {
    name: 'name',
    type: 'gauge',

    // 标题
    title: {
      color: '#6b8bb7',
      offsetCenter: [0, '90%'],
      fontSize: 30,
      textShadowColor: 'rgba(0, 0, 0, .87)',
      textShadowBlur: 65
    },

    // 值
    detail: {
      color: '#009fb7',
      fontSize: 32,
      textShadowColor: '#184fcd',
      textShadowBlur: 15
    },

    splitLine: {
      show: false
    },
    axisLine: {
      lineStyle: {
        color: [[1, '#211633'], [1, 'transparent'], [1, 'transparent']],
        shadowColor: '#1ea099',
        shadowBlur: 6,
        opacity: 1

      }
    },
    axisTick: {
      length: 6,
      lineStyle: {
        color: '#04d8d1'
      }
    },
    axisLabel: {
      show: false
    },

    // 指针样式
    itemStyle: {
      color: '#991a20'
    },
    data: []
  }
}



