export default {
  'url': '/totalTrend/threatenDetail/:type/:id/:limit/:currentPage',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'list|8': [
        {
          'name': '@cname',
          'company': '@company',
          'IP': '@ip',
          'type': '暴力破解',
          'level': '@dangerlevel'
        }
      ]
    }
  }
}



